import { Store, useStore } from "./Store";

// export const authStore = new Store<null | string>(typeof localStorage === "undefined" ? null : localStorage['jwt']);

export const ActiveInstancestore = new Store<false | string>(typeof localStorage === "undefined" ? false : localStorage.getItem('ActiveInstance') ? localStorage.getItem('ActiveInstance') : false);


export const useActiveInstance = () => {
    const [ActiveInstance, setActiveInstance] = useStore(ActiveInstancestore);

    return {
        ActiveInstance, setActiveInstance,
        login(AuthToken: string) {
            setActiveInstance(AuthToken);
        },
        logout() {
            localStorage.clear()
            sessionStorage.clear()
            setActiveInstance(null);
        },
    }
};