import React, { useEffect, useState } from 'react'
import './style.css'
import { useHistory } from 'react-router-dom'
import { loader, } from 'components/Common/core'

import { logostore } from '../../store/logoStore';
import { logopositionstore } from '../../store/logopositionStore';

import { useParams } from 'react-router-dom';
import { useActiveInstance } from '../../store/ActiveInstanceStore';
import { useAuth } from '../../store/authStore';


const LoadingScreen = () => {

  const { logo, loadlogowithid } = logostore();
  const { logoposition, loadlogo_arrayposition_iid } = logopositionstore();
  const history = useHistory();
  const [logoimage] = useState({})
  const { ActiveInstance, setActiveInstance } = useActiveInstance();

  const { auth } = useAuth();

  const firstTimeloader = () => {
    localStorage.setItem("isFirstTime", "true")

    setActiveInstance("37");
    setTimeout(
      () => {
        if (params.instanceId) {
          history.replace(`/${params.instanceId}/StartScreen`)
          localStorage.setItem("ActiveInstance", params.instanceId)
          setActiveInstance(params.instanceId)
        }
        else {
          history.replace(`/37/StartScreen`)
        }
      }, 8000
    );
  }

  const loader = () => {
    localStorage.setItem("isFirstTime", "true")

    setActiveInstance("37");
    setTimeout(
      () => {
        if (auth) {
          if (params.instanceId) {
            history.replace(`/${params.instanceId}/TaskListScreen`)
            localStorage.setItem("ActiveInstance", params.instanceId)

          }
          else {
            // activate scanner
            if (!ActiveInstance)
              history.replace(`/${ActiveInstance}/StartScreen`)
            else
              history.replace(`/37/StartScreen`)


          }
        }
        else {
          if (params.instanceId) {
            history.replace(`/${params.instanceId}/StartScreen`)
          }
          else {
            history.replace(`/37/StartScreen`)
          }
        }
      }, 8000
    );
  }

  let params = useParams();
  useEffect(() => {
    console.log("=====>", ActiveInstance, auth);
    if (params.instanceId) {
      if (!logo) {
        loadlogowithid(params.instanceId)
      }
      if (!logoposition) {
        loadlogo_arrayposition_iid(params.instanceId)
      }
    }
    else {
      if (!logo) {
        loadlogowithid(37)
      }
      if (!logoposition) {
        loadlogo_arrayposition_iid(37)
      }
    }

    if (localStorage.getItem("isFirstTime") === null) {
      firstTimeloader()
    }
    else {
      loader()
    }



  }, [])

  const logoimage_position = ((obj: any) => {
    const selectcase = (number: any) => {
      if (number === 1)
        return "logostyle1";
      else
        if (number > 1 && number < 4)
          return "logostyle2";
        else
          if (number >= 4 && number <= 6)
            return "logostyle3";
          else
            if (number > 6)
              return "logostyle4";
    }
    let a = selectcase(obj.length)
    return obj.map((obj1: any) => {
      return <img className={a} src={obj1.i_logo} />
    })
  })

  return (
    <>

      <div onClick={() => {
        if (localStorage.getItem('isFirsttime') === null) {
          loader()
          history.replace('/StartScreen')
        } else {
          history.replace('/TaskListScreen')
        }
      }}>
        <div className='loadbgwrapper'>

          <div className="row1">
            <div className="loadinnerrow">
              {(logoposition) && logoposition.section1.map(logoimage_position)}
            </div>
          </div>

          <div className='row2'>
            <div className="innerrowtitle"><u>CONTENT</u></div>
            <div className="loadinnerrow">
              {(logoposition) && logoposition.section2.map(logoimage_position)}
            </div>
          </div>

          <div className='row3'>
            <div className="innerrowtitle"><u>GAME APP DEVELOPMENT</u></div>
            <div className="loadinnerrow">
              {(logoposition) && logoposition.section3.map(logoimage_position)}
            </div>
          </div>

          <div className='row4'>
            <img className='loadbgBelow' src={logoimage.position === 10 && logoimage.i_logo} />
          </div>

        </div>
      </div>

    </>
  )
}

export default LoadingScreen
